.search {
  width: 327px;
  height: 64px;
  position: relative;

  &__input {
    width: 100%;
    height: 100%;
    background-color: white;
    box-shadow: -1px 0 0 0 $light-gray;
    box-sizing: border-box;
    padding-left: $search-padding;

    &:focus {
      background-color: #fafbfc;
      box-shadow: -1px 0 0 0 $light-gray;
      outline: none;
    }

    &::placeholder {
      color: $middle-gray;
      font-family: Mont, sans-serif;
      font-size: 14px;
      font-style: normal;
      font-weight: 600;
      line-height: normal;
    }
  }

  &__label {
    position: absolute;
    width: 16px;
    height: 16px;
    right: $search-padding;
    top: 32px;
    transform: translate(-50%, -50%);
  }
}
